
.main {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.site-title {
  font-family: 'Droid Serif', serif;
  transition: all 1s;
  position: relative;
  top:0;
  padding-right: 0.5em;
}
@media (max-width: 500px){
.site-title-main{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 0;
    margin: 0;
}
.main{
    -ms-flex-align: center;
        align-items: center;
}
}
.component-container{
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  z-index: -1;
}
.component-container>*{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.site-title-main{
  color: rgba(0, 0, 0, 0.8);
  font-size: 4em;
  top:15%;
  text-shadow: 0px 0px 6px rgba(212, 166,50,0.6 );
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s
}
.fade-enter,
.fade-leave-active {
  opacity: 0
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}



